import {mapState} from 'vuex';
import appText from '../../components/basic-component/app-rich/parse.vue';

export default {
    name: "recharge",
    computed: {
        ...mapState({
            customPage: state => state.mallConfig.recharge_page_custom,
            appImg: state => state.mallConfig.__wxapp_img,
            userInfo: state => state.user.info,
        }),
    },
    data() {
        return {
            setting: null,
            list: null,
            pay_price: '',
            balance: '￥',
            selected: -1,
            getFocus: false,
            pageShow: false,
        }
    },
    onShow: function () {
        this.loadData();
    },

    components: {
        appText
    },
    watch: {
        pay_price: {
            handler: function (newData, oldData) {
                if (newData) {
                    this.selected = -1;
                }
            }
        }
    },

    methods: {
        navMember: function (member) {
            uni.navigateTo({
                url: `/pages/member/upgrade/upgrade?level=${member.level}`
            });
        },
        getSetting: function () {
            const self = this;
            self.$request({
                url: self.$api.balance.index,
            }).then(info => {
                if (info.code === 0) {
                    self.pageShow = true;
                    if (info.data.setting.status === `1`) {
                        self.setting = info.data.setting;
                        self.balance = '￥' + info.data.balance;
                    } else {
                        uni.showModal({
                            title: '提示',
                            content: '尚未开启余额功能',
                            showCancel: false,
                            confirmText: '确定',
                            success(e) {
                                if (e.confirm) {
                                    uni.navigateBack({delta: 1})
                                }
                                }
                            });
                        }
                    }
                })
            },
            loadData: function () {
                const self = this;
                self.getSetting();
                self.$showLoading({title: `加载中`});
                self.$request({
                    url: self.$api.recharge.index,
                }).then(info => {
                    self.$hideLoading();
                    if (info.code === 0) {
                        self.list = info.data.list;
                    }
                }).catch(info => {
                    self.$hideLoading();
                })
            },

            bindFocus: function () {
                this.selected = -1;
                this.getFocus = true;
            },

            bludBlur: function () {
                this.getFocus = false;
            },

            changeInput(e) {
            },
            setPrice: function (index) {
                this.pay_price = '';
                this.selected = index;
            },

            submit: function () {
                const self = this;
                let para;
                if (self.selected === -1) {
                    if (self.pay_price > 0) {
                        para = {
                            id: 0,
                            pay_price: self.pay_price,
                            send_price: 0
                        };
                    } else {
                        uni.showToast({
                            title: '金额不能为空',
                            icon: 'none',
                        });
                        return;
                    }
                } else {
                    para = {
                        id: self.list[self.selected].id,
                        pay_price: self.list[self.selected].pay_price,
                        send_price: self.list[self.selected].send_price,
                    }
                }

                self.$showLoading({title: `加载中`});
                self.$request({
                    url: self.$api.recharge.balance_recharge,
                    data: para,
                    method: 'POST',
                }).then(info => {
                    self.$hideLoading();
                    if (info.code === 0) {
                        self.$payment.pay(info.data.pay_id).then(e => {
                            uni.showModal({
                                title: '提示',
                                content: '充值成功！',
                                showCancel: false,
                                confirmText: '确定',
                                success(e) {
                                    if (e.confirm) {
                                        uni.navigateBack({delta: 1})
                                    }
                                }
                            });
                        }).catch(e => {
                            //uni.showToast({'title': e, icon: 'none'});
                        });
                    } else {
                        uni.showToast({'title': info.msg, icon: 'none'});
                    }
                }).catch(info => {
                    self.$hideLoading();
                })
            }
        }
}
